html {
  font-family: "Gothic A1", sans-serif;
  font-family: "Mukta", sans-serif;
  line-height: 1.5;
  background-color: var(--gray-900);

  word-break: keep-all;
  overflow-x: hidden;

  -ms-overflow-style: none;
}
html::-webkit-scrollbar {
  display: none;
}

* {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img,
iframe {
  display: block;
}
