.content__container{
  object-fit: contain;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
  background-color: #39445A;
  color: #fff;
}
/* хром, сафари */
.content__container::-webkit-scrollbar { width: 0; }
/* ie 10+ */
.content__container { -ms-overflow-style: none; }
/* фф (свойство больше не работает, других способов тоже нет)*/
.content__container { overflow: -moz-scrollbars-none; }


.content__poster{
  position: relative;
  border-radius: 10px;
  /*padding: 0 0 150px 0;*/
  display: flex;
  overflow: hidden;
  flex: 1 1 auto;
}
.back__poster{
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  /*justify-self: flex-start;*/
  /*align-self: start;*/
  border-radius: 10px;
  -o-object-fit: cover;
     object-fit: cover;
  /*width: 100%;*/

  /*object-fit: cover;*/
}

