:root {
  --primary: #032541;
  --secondary: #21d07a;
  --added: #ffff29;
  --remove: #f81313;
  --white: #f2ff4d;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

.header {
  display: grid;
  grid-template: 1fr / auto 1fr auto auto auto minmax(10px, 10px);
  max-height: 2rem;
  padding: 0.15em 0.15em;
}
@media (max-width: 767px) {
  .header {
    font-size: calc(16px + 13.6 * ((100vw - 320px) / 1180));
  }
}
@media (min-width: 767px) {
  .header {
    font-size: calc(16px + 8 * (100vw / 1180));
  }
}
.header span {
  margin: 0 0.25rem 0 0;
  opacity: 0.75;
}
.header .fa-imdb {
  vertical-align: middle;
}
.header .header__button {
  padding: 0 0.5em 0.5rem 0;
  display: flex;
  align-items: center;
  min-height: 2rem;
  text-shadow: 1px 1px 2px #c54040, -1px -1px #c74b16;
}
@media (max-width: 767px) {
  .header .header__button {
    font-size: calc(12px + 6.8 * ((100vw - 320px) / 1180));
  }
}
@media (min-width: 767px) {
  .header .header__button {
    font-size: calc(12px + 4 * (100vw / 1180));
  }
}

.content_wrapper {
  padding: 10px;
  position: relative;
  top: 100px;
}

.movie_card {
  height: 100%;
}

.handler {
  position: relative;
}
.handler:hover .movie {
  background-color: white;
  color: black;
}
.handler:hover .single_movie__icon {
  opacity: 0.8;
  text-shadow: 3px 3px 10px #272634;
}

@media (min-width: 991px) {
  .single_movie__icon {
    position: absolute;
    z-index: 5;
    left: 0.45em;
    opacity: 0.05;
    top: 0.45em;
    height: 50px;
  }

  .single_movie__icon.fa-bookmark,
  .single_movie__icon.fa-bookmark-o,
  .single_movie__icon.fa-times-circle {
    left: 1.85em;
  }

  .content_focus__icon:hover,
  .single_movie__icon:hover {
    color: #21d07a;
    color: var(--secondary);
  }

  .content_focus__icon.fa:hover,
  .single_movie__icon.fa:hover {
    color: #21d07a;
    color: var(--secondary);
  }
  .content_focus__icon.fa-bookmark,
  .single_movie__icon.fa-bookmark {
    color: #ffff29;
    color: var(--added);
  }
  .content_focus__icon.fa-bookmark:hover,
  .content_focus__icon.fa-eye-slash:hover,
  .single_movie__icon.fa-bookmark:hover,
  .single_movie__icon.fa-eye-slash:hover {
    color: #f81313;
    color: var(--remove);
  }

  .watch_page__icon.fa-times-circle:hover {
    color: #f81313;
    color: var(--remove);
  }
}
@media (max-width: 991px) {
  .single_movie__icon {
    display: none;
  }

  .content_focus__icon {
    position: relative;
  }
}
