html {
  font-family: "Gothic A1", sans-serif;
  font-family: "Mukta", sans-serif;
  line-height: 1.5;
  background-color: var(--gray-900);

  word-break: keep-all;
  overflow-x: hidden;

  -ms-overflow-style: none;
}
html::-webkit-scrollbar {
  display: none;
}

* {
  margin: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

img,
iframe {
  display: block;
}

:root {
    --black: hsl(0, 0%, 0%);
    --gray-900: hsl(0, 0%, 8%);
    --gray-800: hsl(0, 0%, 13%);
    --gray-700: hsl(0, 0%, 20%);
    --gray-400: hsl(0, 0%, 45%);
    --gray-100: hsl(0, 0%, 90%);
    --white: hsl(0, 0%, 100%);
    --red: hsl(357, 92%, 46%);
    --orange: hsl(32, 98%, 46%);
  }
